document.addEventListener("DOMContentLoaded", function () {
    var mySwiper = new Swiper(".text-block-with-swiper__swiper-block", {
      allowTouchMove: false,
      slidesPerView: 1,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev",
      },
    });
  });